<template>
  <BaseLayout title="我的关注">
    <van-pull-refresh v-model="state.refreshing" @refresh="onRefresh">
      <van-list class="list" v-model:loading="state.loading" :finished="state.finished" loading-text="正在请求数据 ..." finished-text="没有更多了" :immediate-check="false" @load="onLoad">
        <mall-item :grid="state.grid" :item="item" v-for="(item, index) in state.list" :key="index" :cart="false" :sold="false" :stock="false"></mall-item>
        <van-empty v-show="state.list.length == 0" :style="{width:'100%'}"></van-empty>
      </van-list>
    </van-pull-refresh>
  </BaseLayout>
</template>

<style scoped>
.list {
  display: flex;
  flex-flow: row wrap;
  align-content: flex-start;
  min-height: 500px;
}
</style>

<script>
import {reactive} from 'vue'
import BaseLayout from '@/components/BaseLayout.vue'
import MallItem from '@/components/MallItem.vue'

export default {
  components: {BaseLayout, MallItem},

  setup() {
    const state = reactive({
      refreshing: false,
      loading: false,
      finished: false,
      grid: true,
      list: [],
      offset: 0,
      limit : 20
    });
    return {state};
  },

  mounted(){
    this.onRefresh();
  },

  methods: {
    onRefresh() {
      this.state.loading = true;
      this.state.finished = false;
      this.state.offset = 0;
      this.state.list = [];
      this.onLoad();
    },
    onLoad() {
      this.$http.get("/mall/interestList?type=1&offset=" + this.state.offset + "&limit=" + this.state.limit).then((data) => {
        this.state.refreshing = false;
        if (data.length == 0)
          return this.state.finished = true;
        this.state.loading = false;
        this.state.offset += data.length;

        for (let i in data){
          let item = data[i];
          this.state.list.push({...item, ...{
            cover: item.image,
          }});
        }
      });
    },
  }
}
</script>
